<template>
	<div class="eval_container">
		<div class="title"><img src="/img/back_2.png" @click="back">发表评价 <div class="submit" @click="submit">发布</div></div>
		<div class="product_eval">
			<Score  length="5" v-model="productScore" class="score">
				<img :src="productImg" alt="" class="product_img">
				<p>描述相符:</p>
			</Score>
		</div>
		<div class="eval_text">
			<textarea placeholder="该产品符合你的预期吗？来说说它的优点和不足吧！"></textarea>
		</div>
		<div class="shop_eval">
			<div class="shop_title">
				<img src="/img/shop.png" alt="">
				<span>店铺评分</span>
			</div>
			<Score length="5" v-model="serverScore" class="shop_score">服务态度：</Score>
			<Score length="5" v-model="expressScore" class="shop_score">物流速度：</Score>

		</div>
	</div>
</template>

<script>
import Score from './score.vue'
import {Toast} from 'mint-ui'
	export default{
		components: { Score },
		data(){
			return{
				productScore: 0,
				serverScore: 0,
				expressScore: 0,
				productImg: "/img/product.jpg"
			}
		},
		methods: {
			back: function(){
				history.go(-1)
			},
			submit: function(){
				if(this.productScore == 0){
					Toast("请对产品描述相符进行评分")
				}
				else if(this.serverScore == 0){
					Toast("请对服务态度进行评分")
				}
				else if(this.expressScore == 0){
					Toast("请对物流速度进行评分")
				}else{
					Toast("发布成功");
					setTimeout(function(){
						history.go(-1);
					}, 1000)
				}

			}
		}
	}
</script>

<style lang="scss">
@import "../style.scss";
*{
	margin: 0;
	padding: 0;
}
.eval_container{
	width: 100%;
	max-width: 640px;
	margin: 0 auto;
}
.title{
	width: 100%;
	height: 45px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	img{
		width: 30px;
		height: 30px;
	}
	color: #939;
	.submit{
		font-size: 0.8rem;
		width: 40px;
	}
}
.product_eval{
	width: 100%;
	border-bottom: 1px solid #f2f2f2;
}
.score{
	width: 75%;
	margin-left: 1%;
	.product_img{
		width: 30px;
		height: 30px;
	}
	p{
		color: #666;
	}
}
.eval_text{
	width: 98%;
	margin: 0 auto;
	padding-top: 2%;
	textarea{
		width: 100%;
		height: 120px;
		border: none;
		background-color: #fafafa;
		font-size: 1.05rem;
		box-sizing: border-box;
		padding: 5px 10px;
		color: #333;
	}
}
.shop_eval{
	width: 100%;
	margin: 4%;
	.shop_title{
		display: flex;
		align-items: center;
		height: 40px;
		margin-left: 1%;
		img{
			width: 25px;
			height: 25px;
		}
		span{
			padding: 0 5px;
		}
	}
}
.shop_score{
	width: 60%;
	color: #666;
}
</style>


