<template>
    <div class="score_container">
    	<slot></slot>
        <div class="score_item" v-for="n in lg">
            <img src="/img/bottle_empty.png" @click="mark(n)" v-show="!full[n]">
            <img src="/img/bottle_fulled.png" @click="mark(n)" v-show="full[n]" class="fulled">
        </div>
    </div>
</template>
<script>
export default {
    props: ["length", "imgUrl"],
    data() {
        return {
            lg: parseInt(this.length),
            full: []
        }
    },
    created: function() {
        var ln = this.lg;
        for (var i = 0; i < ln; i++) {
            this.full.push(false)
        }
    },
    methods: {
        mark: function(n) {
            this.$emit("input", (n + 1));
            for (var i = 0; i < (this.lg + 1); i++) {
                this.full.splice(i, 1, false);
            }
            for (var i = 0; i < (n + 1); i++) {
                this.full.splice(i, 1, true);
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.score_container {
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.score_item {
    width: 25px;
    height: 25px;
    img {
        width: 25px;
        height: 25px;
    }
    .fulled {
        animation: scaleIn 0.2s;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(0.5)
    }
    100% {
        transform: scale(1)
    }
}
</style>


